import React, { useEffect, useState } from "react";
import Intro from "../components/Intro";
import "../styles/homepage.scss";

let loadingInterval = null;

export default function Home() {
  const [loaderPercentage, setLoaderPercentage] = useState(0);

  useEffect(() => {
    loadingInterval = setInterval(() => {
      setLoaderPercentage((lp) => {
        const p = lp + Math.floor(Math.random() * 5);
        return p > 100 ? 100 : p;
      });
    }, 100);
  }, []);

  useEffect(() => {
    if (loaderPercentage >= 100) {
      clearInterval(loadingInterval);
    }
  }, [loaderPercentage]);

  if (loaderPercentage < 100) {
    return (
      <div className="home-content">
        <h1 className="percentage">{loaderPercentage}%</h1>
      </div>
    );
  }
  return (
    <>
      <Intro />
      <div className="intro-details">
        <p className="big">
          If you have any <strong>project</strong> request,{" "}
          <strong>query</strong>, or an <strong>idea</strong> to discuss, feel
          free to <a href="mailto:hi@shakil.info">say hi</a>!
        </p>
      </div>
      <div className="side-bar">
        <div className="marquee">
          <p>
            <span>
              &nbsp;<b>UI</b>/<b>UX</b>&nbsp;DESIGNER | DEVELOPER | FREELANCER |
              CONSULTANT |
            </span>
            <span>
              &nbsp;<b>UI</b>/<b>UX</b>&nbsp;DESIGNER | DEVELOPER | FREELANCER |
              CONSULTANT |
            </span>
            <span>
              &nbsp;<b>UI</b>/<b>UX</b>&nbsp;DESIGNER | DEVELOPER | FREELANCER |
              CONSULTANT |
            </span>
          </p>
        </div>
      </div>
    </>
  );
}
