import React, { useEffect } from "react";
import { gsap } from "gsap";
import sa from "../images/shakil-ahmmed.jpg";
import useLinkCursor from "../hooks/useLinkCursor";

export default function Intro() {
  useLinkCursor();
  useEffect(() => {
    gsap.set(".intro-wrapper", { autoAlpha: 0, yPercent: 100 });
    gsap.to(".intro-wrapper", {
      yPercent: 0,
      autoAlpha: 1,
      duration: 0.5,
      ease: "linear",
    });
    const introText = document.querySelector(".intro-title");
    const image = document.getElementById("intro-hover");
    const cursor = document.getElementById("cursor");
    const follow = document.getElementById("follow");
    introText.addEventListener("mouseover", () => {
      image.style.opacity = "1";
      cursor.style.opacity = "0";
      follow.style.opacity = "0";
    });
    introText.addEventListener("mousemove", (e) => {
      image.style.top = e.clientY + "px";
      image.style.left = e.clientX + "px";
    });
    introText.addEventListener("mouseleave", () => {
      image.style.opacity = "0";
      cursor.style.opacity = "1";
      follow.style.opacity = "1";
    });
  }, []);
  return (
    <>
      <div className="intro-wrapper">
        <div className="intro-title">
          <img src={sa} alt="Shakil Ahmmed" id="intro-hover" />
          <h1>
            Hello<span>.</span>
          </h1>
          <h2>
            I am Shak
            <span>!</span>l
          </h2>
          <div className="lr">
            <div className="lr_inner">
              <div className="lr_content">
                Full Stack <strong>WEB</strong> and{" "}
                <strong>MOBILE APPLICATION</strong> Developer
              </div>
            </div>
          </div>
        </div>
        <div className="intro-details">
          <p className="secondary">
            My area of expertise primarily includes{" "}
            <strong className="react">React</strong>
            <small> (w/ Gatsby, Next)</small>, and{" "}
            <strong className="react">React Native</strong>. I have a
            professional background in{" "}
            <strong className="mui">Material UI</strong>,{" "}
            <strong className="semantic-ui">Semantic UI</strong>,{" "}
            <strong className="tailwind">Tailwind CSS</strong>,{" "}
            <strong className="laravel">Laravel</strong>,{" "}
            <strong className="node">Node.js</strong>,{" "}
            <strong className="mysql">
              <span>My</span>
              <span>SQL</span>
            </strong>
            , <strong className="mongo">MongoDB</strong>, and server managements
            <small>
              {" "}
              (w/ <strong className="aws">AWS</strong>,{" "}
              <strong className="nginx">Nginx</strong>,{" "}
              <strong className="apache">Apache</strong>, and{" "}
              <strong className="litespeed">LiteSpeed</strong>)
            </small>
            .
          </p>
        </div>
      </div>
    </>
  );
}
